// after
.after-divider-center {
  @include after-divider;
}

.after-divider-start {
  @include after-divider{
    top: 80%;
    left: 43%;

    @include desktop {
      left: 0%;
    }
  };
}

.after-divider-white {
  @include after-divider-white {
    @include desktop {
      left: 0%;
    }
  }
}

// shade
.shade-tertiary {
  background-color: rgba(28, 28, 28, 0.9);
}
