.banner {
  background: url('../images/bg_home_mobile.jpg')  center center no-repeat;
  background-size: cover;
  width: 100% ;
  height: 680px;

  @include desktop {
    background: url('../images/bg_home.jpg');
    background-size: contain;
  }

  .square {
    width: 10px;
    height: 10px;
    background-color: #fff;
  }

  h2 {
    font-size: 48px;
    line-height: 1.5;
  }

  p {
    font-size: 24px;
    line-height: 35px;
    font-weight: 500;

    @include desktop {
      font-size: 48px;
      line-height: 70px;
    }

    &:last-child {
      font-size: 14px;
      line-height: 20px;
      font-weight: 300;

      @include desktop {
        font-size: 18px;
        line-height: 26px;
      }
    }
  }
}

.course {
  h2 {
    font-size: 24px;
    line-height: 35px;
  }

  .card {
    &:hover {
      color: #ffffff;
      background: $black-500;
      border: 1px solid $primary;
      cursor: pointer;
      transition: 0.5s;

      .material-icons {
        @include text-gradient;
      }
    }
  }
}

.sop {
  ul > li{
    &:nth-of-type(1){
      .card {
        @include after-number {
          content: '01';
        }
      }
    }
    &:nth-of-type(2){
      .card {
        @include after-number {
          content: '02';
        }
      }
    }
    &:nth-of-type(3){
      .card {
        @include after-number {
          content: '03';
        }
      }
    }
    &:nth-of-type(4){
      .card {
        @include after-number {
          content: '04';
        }
      }
    }
  }
}

.hot {
  .card {
    border-radius: 24px;

    &:hover {
      border: 1px solid $primary;
      transition: 0.5s;
    }

    figure {
      width: 100px;
      height: 100px;
    }

    h4 {
      font-size: 20px;
      line-height: 29px;
    }
  }

  .btnbtn {
    color: #ffffff;
    background: linear-gradient(104deg, #09ACF5, #62DB54);
    border: 1px solid transparent;

    &:hover {
      background: linear-gradient(104deg, #09ACF5, #62DB54);;
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      border: 1px solid $primary;
    }
  }
}

.info {
  min-width: 100%;
  height: 615px;
}
.info-group > li {
  &:hover {
    color: #ffffff;
    .material-icons {
      color: $primary;
    }
    .after-divider-white {
      &:after {
        background-color: $primary;
      }
    }
    .shade-tertiary {
      background-color: rgba(28, 28, 28, 0.12);
    }
  }

  &:nth-of-type(1) {
    background:  url('../images/info01.jpg') center center / cover ;
  }
  &:nth-of-type(2) {
    background:  url('../images/info02.jpg') center center / cover ;
  }
  &:nth-of-type(3) {
    background:  url('../images/info03.jpg') center center / cover ;
  }
  &:nth-of-type(4) {
    background:  url('../images/info04.jpg') center center / cover ;
  }


}

.article {
  .squareRounded {
    display: none;
    width: 32px;
    height: 32px;
    &-topLeft {
      border-top: 2px solid $primary;
      border-left: 2px solid $primary;
      border-top-left-radius: 10px;
    }
    &-topRight {
      border-top: 2px solid $primary;
      border-right: 2px solid $primary;
      border-top-right-radius: 10px;
    }
    &-bottomLeft {
      border-bottom: 2px solid $primary;
      border-left: 2px solid $primary;
      border-bottom-left-radius: 10px;
    }
    &-bottomRight {
      border-bottom: 2px solid $primary;
      border-right: 2px solid $primary;
      border-bottom-right-radius: 10px;
    }
  }

  .card {
    background: #000;

    &:hover {
      .squareRounded {
        display: block;
      }
    }
  }
}
